<!--main-->
<template>
  <div>
    <v-chart
      ref="ref_echart1"
      :option="option_column"
      :style="{ height: getHeight() }"
    ></v-chart>
  </div>
</template>
<script>
import "echarts/lib/chart/line";
import "echarts/lib/component/tooltip";
import { initData } from "../../api/inidata";

export default {
  props: {
    options: {
      type: Object,
    },
    height: {
      type: String,
    },
  },
  data() {
    return {
      option_column: {},
      sexLineData: {},
    };
  },
  created() {
    this.getData();
  },
  mounted() {
    window.addEventListener("resize", () => this.$refs.ref_echart1.resize());
  },

  methods: {
    getHeight() {
      return this.height;
    },
    getData() {
      initData(
        "http://47.100.166.173:8099/qujiangqu/api/users/statistics/sex"
      ).then((res) => {
        console.log(res);
        this.sexLineData = res.data;
        this.option_column = {
          tooltip: {
            trigger: "axis",
          },
          color: ["#c44b7d", "#3fafec"],
          legend: {
            data: ["男", "女"],
            textStyle: {
              color: "#999999",
              fontSize: "20px",
            },
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "4%",
            containLabel: true,
          },
          toolbox: {},
          xAxis: {
            type: "category",
            boundaryGap: false,
            data: [
              this.sexLineData.xaxis[0],
              this.sexLineData.xaxis[1],
              this.sexLineData.xaxis[2],
              this.sexLineData.xaxis[3],
              this.sexLineData.xaxis[4],
            ],
          },
          yAxis: {
            type: "value",
          },
          series: [
            {
              name: "女",
              type: "line",
              data: [
                this.sexLineData.yaxises[1][0],
                this.sexLineData.yaxises[1][1],
                this.sexLineData.yaxises[1][2],
                this.sexLineData.yaxises[1][3],
                this.sexLineData.yaxises[1][4],
              ],
            },
            {
              name: "男",
              type: "line",
              data: [
                this.sexLineData.yaxises[0][0],
                this.sexLineData.yaxises[0][1],
                this.sexLineData.yaxises[0][2],
                this.sexLineData.yaxises[0][3],
                this.sexLineData.yaxises[0][4],
              ],
            },
          ],
        };
      });
    },
  },
};
</script>



