                    style="width:110px;font-size: 18px;height:202px;color: #222222;line-height: 18px;justify-content: center;position: absolute;top: 40px;left: 60px">
                    <div style="width:100px;text-align: center">分别统筹</div>
                    <div>网格临时管控</div>
                    <div>网格人员转运</div>
                    <div>网格核酸检测</div>
                    <div>网格物资配送</div>
                    <div>网格需求收集</div>
                  </div>
                </div>
                <div
                  style="width: 90px;height: 202px;background: #FCD1D4;border-radius: 3px;float: left;text-align: center;margin-left: 5px">
                  <div
                    style="width:90px;font-size: 18px;height:202px;font-weight: 500;color: #222222;line-height: 20px;padding-top: 40px">
                    <div>统筹整村</div>
                    <div>信息收集</div>
                    <div>及报送(数</div>
                    <div>字化、信</div>
                    <div>息化类工</div>
                    <div>作)</div>
                  </div>
                </div>
                <div
                  style="width: 84px;height: 202px;background: #FCD1D4;border-radius: 3px;float: left;text-align: center;margin-left: 5px">
                  <div
                    style="width:84px;font-size: 18px;height:202px;font-weight: 500;color: #222222;line-height: 20px;padding-top: 40px">
                    <div>物资配送</div>
                    <div>人员转运</div>
                    <div>秩序维护</div>
                    <div>卡口值班</div>
                    <div>核酸检测</div>
                  </div>
                </div>
                <div
                  style="width: 164px;height: 202px;background: #FCD1D4;border-radius: 3px;float: left;text-align: center;margin-left: 5px">
                  <div
                    style="width:164px;font-size: 18px;height:202px;font-weight: 500;color: #222222;line-height: 20px;padding-top: 40px">
                    <div>最小作战单元</div>
                    <div>摸清人口底数</div>
                    <div>组织核酸检测</div>
                    <div>发放生活物资</div>
                    <div>收集需求信息</div>
                  </div>
                </div>
              </div>
            </div>
            <div style="float: right;width: 220px;height: 100px;position: absolute;top: 190px;right: 9px">
              <div
                style="width: 217px;height: 57px;background: #B18DCB;border-radius: 3px;float: left;text-align: center;margin-left: 5px">
                <div
                  style="font-size: 18px;height:57px;font-weight: 500;color: #ffffff;line-height: 57px;text-align: center;display: flex;justify-content: center">
                  制度保障
                </div>
              </div>
              <div
                style="width: 51px;height: 452px;background: #D1BCDD;border-radius: 3px;float: left;text-align: center;margin-left: 5px;margin-top: 8px">
                <div
                  style="font-size: 18px;height:452px;font-weight: 500;color: #222222;line-height: 51px;writing-mode: tb-rl;display: flex;">
                  <div style="margin-top: 50px">
                    五色管理机制
                  </div>
                  <div style="margin-top: 30px">在册人口</div>
                  <div style="margin-top: 50px">流动人口</div>
                </div>
              </div>
              <div
                style="width: 50px;height: 452px;background: #D1BCDD;border-radius: 3px;float: left;text-align: center;margin-left: 5px;margin-top: 8px">
                <div
                  style="font-size: 18px;height:452px;font-weight: 500;color: #222222;line-height: 51px;writing-mode: tb-rl;display: flex;">
                  <div style="margin-top: 50px">
                    应急管理机制
                  </div>
                  <div style="margin-top: 30px">人员</div>
                  <div style="margin-top: 30px">物资</div>
                  <div style="margin-top: 30px">车辆</div>
                </div>
              </div>
              <div
                style="width: 50px;height: 452px;background: #D1BCDD;border-radius: 3px;float: left;text-align: center;margin-left: 4px;margin-top: 8px">
                <div
                  style="font-size: 18px;height:452px;font-weight: 500;color: #222222;line-height: 51px;writing-mode: tb-rl;display: flex;">
                  <div style="margin-top: 50px">
                    指令传导机制
                  </div>
                  <div style="margin-top: 30px">乡镇</div>
                  <div style="margin-top: 30px">社区</div>
                  <div style="margin-top: 30px">网格</div>
                </div>
              </div>
              <div
                style="width: 50px;height: 452px;background: #D1BCDD;border-radius: 3px;float: left;text-align: center;margin-left: 4px;margin-top: 8px">
                <div
                  style="font-size: 18px;height:452px;font-weight: 500;color: #222222;line-height: 50px;writing-mode: tb-rl;display: flex;">
                  <div style="margin-top: 50px">
                    问题销号机制
                  </div>
                  <div style="margin-top: 20px">统一汇总</div>
                  <div style="margin-top: 20px">逐个交办</div>
                  <div style="margin-top: 20px">反馈销号</div>
                </div>
              </div>
            </div>
          </div>
        </el-dialog>
        <div class="statistics">
          <div class="page">
            <div class="name">
              人员统计
            </div>
            <lines-chart :options="sexLineData" :height="'300px'"></lines-chart>
          </div>
        </div>
        <div class="radar">
          <div class="daibiao">
            <div class="radar-title" style="position: absolute;top: 30px;left: 20px">村民代表</div>
            <div class="radar-num" style="position: absolute;top: 60px;left: 50px">{{rightData['村民代表']}}</div>
          </div>
          <div class="dangyuan">
            <div class="radar-title" style="position: absolute;top: 35px;left: 44px">党员</div>
            <div class="radar-num" style="position: absolute;top: 60px;left: 50px">{{rightData['党员']}}</div>
          </div>
          <div class="zhongdian">
            <div class="radar-title" style="position: absolute;top: 40px;left: 30px">特殊人群</div>
            <div class="radar-num" style="position: absolute;top: 70px;left: 65px">{{rightData['特殊人群']}}</div>
          </div>
          <div class="dishouru">
            <div class="radar-title" style="position: absolute;top: 40px;left: 30px">低保户</div>
            <div class="radar-num" style="position: absolute;top: 66px;left: 55px">{{rightData['低保户']}}</div>
          </div>
          <div class="canjiren">
            <div class="radar-title" style="position: absolute;top: 25px;left: 25px">残疾人</div>
            <div class="radar-num" style="position: absolute;top: 53px;left: 45px">{{rightData['残疾人']}}</div>
          </div>
        </div>
      </div>
    </dv-full-screen-container>
  </div>
</template>

<script>
import PieChart from '../../components/PieChart/index'
import LinesChart from '../../components/LineChart/index'
import {initData} from "../../api/inidata";

export default {
  name: "index",
  components: {
    PieChart,
    LinesChart
  },
  data() {
    return {
      pieData:[],
      rightData:{},
      centerDialogVisible: false,
      dangzhibuDialogVisible: false,
      wuseguanliDialogVisible: false,
      weiwanggeDialogVisible: false,
      zhanshiDialogVisible:false,
      wanggezhangDialogVisible:false,
      zhuanzhiwanggeyuanDialogVisible:false,
      jianzhiweiwanggeyuanDialogVisible:false,
      wanggezhidaoyuanDialogVisible:false,
      weiwanggeData: [
        {
          id: 1, wanggemingchen: "九宫微格", num: "1幢-9幢", zhiwu: "微网格员", name: '蓝显刚', phone: '13515704353'
        }
      ],
      wuseData: [],
      houseHold: [],
      srcList: [
        '/static/jiagou.png'
      ],
      secondDialogVisible: false,
      thirdDialogVisible: false,
      loading: true,
      time: '',
      date: '',
      week: '',
      title: '数字白马·基层单元智治中心',
      sexLineData:{},
    }
  },
  created() {
    this.index()
  },
  mounted() {
    this.cancelLoading();
    setInterval(this.getTime, 1000);
  },
  methods: {
    wuseHandle(type) {
      const params = {
        houseHold: type,
        pageIndex:1,
        pageSize:500
      }
      initData('http://47.100.166.173:8099/qujiangqu/api/users/page', params).then(res => {
        this.wuseData = res.data.list
      })
      this.wuseguanliDialogVisible = true
    },
    index() {
      initData('http://47.100.166.173:8099/qujiangqu/api/users/statistics/house-hold').then(res => {
        this.houseHold = res.data.lineBarDataVo
        this.pieData=res.data.pieDataVo.data

      })
      initData('http://47.100.166.173:8099/qujiangqu/api/users/statistics/type/count').then(res => {
          this.rightData=res.data
      })

    },
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    getTime() {
      var date = new Date();
      var YY = date.getFullYear();
      var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
      var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
      var hh = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours());
      var mm = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());
      var ss = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
      var myddy = date.getDay();//获取存储当前日期
      var weekday = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
      this.time = hh + ':' + mm + ':' + ss;

      this.date = YY + '年' + MM + '月' + DD + '号';
      this.week = weekday[myddy]
    },
  }
}
</script>

<style lang="scss">
@import "../../assets/style/index.scss";

.header-box {
  width: 100%;
  height: 15vh;
  position: relative;

  .return {
    position: absolute;
    top: 5rem;
    right: 200px;
    width: 100px;
    height: 50px;
    border: 2px solid #999999;
    border-radius: 3px;
    display: flex;

    span {
      display: flex;
      font-size: 20px;
      vertical-align: center;
      align-items: center;
    }
  }

  .time-box {
    width: 15%;
    height: 5vh;
    position: absolute;
    top: 5rem;
    left: 3rem;

    > div {
      display: inline-block;
    }

    .time {
      color: #64E5CE;
      font-size: 28px;
      margin-left: 0.2rem;
    }

    .date {
      color: #DAF4EF;
      font-size: 12px;
    }
  }

  .title-box {
    position: absolute;
    top: 2.5rem;
    left: 39%;

    span {
      color: #ffffff;
      font-size: 40px;
      font-weight: bold;
      display: flex;
    }
  }
}

.content-box {
  height: 100%;
  padding: 10px 30px 0 30px;
  position: relative;

  .name {
    font-size: 20px;
    color: #18A1DF;
  }

  .constitute {
    width: 100%;
    height: 400px;
    //border: 1px solid #257dff;
    background-color: rgba(16, 27, 47, 0.8);
    display: flex;

    .left-page {
      display: inline-block;
      width: 30%;
      padding-left: 80px;
      padding-top: 20px;
    }

    .right-page {
      display: inline-block;
      width: 50%;
    }
  }

  .statistics {
    margin-top: 2rem;
    width: 48%;
    height: 350px;
    float: left;
    //border: 1px solid #257dff;
    background-color: rgba(16, 27, 47, 0.8);

    .page {
      padding-left: 80px;
      padding-top: 20px;
    }
  }

  .radar {
    margin-top: 2rem;
    width: 48%;
    height: 350px;
    float: right;
    position: relative;
    //border: 1px solid #257dff;
    background-color: rgba(16, 27, 47, 0.8);

    .daibiao {
      width: 120px;
      height: 120px;
      position: absolute;
      left: 120px;
      top: 40px;
      background-image: url("/static/cmdb.png");
      background-repeat: no-repeat;
    }

    .dangyuan {
      width: 130px;
      height: 130px;
      position: absolute;
      left: 390px;
      top: 50px;
      background-image: url("/static/dy.png");
      background-repeat: no-repeat;
    }

    .zhongdian {
      width: 140px;
      height: 140px;
      position: absolute;
      right: 150px;
      top: 110px;
      background-image: url("/static/zdry.png");
      background-repeat: no-repeat;
    }

    .dishouru {
      width: 130px;
      height: 130px;
      position: absolute;
      left: 190px;
      top: 150px;
      background-image: url("/static/dsrnh.png");
      background-repeat: no-repeat;
    }

    .canjiren {
      width: 130px;
      height: 130px;
      position: absolute;
      left: 400px;
      top: 200px;
      background-image: url("/static/cjr.png");
      background-repeat: no-repeat;
    }

    .radar-title {
      font-size: 20px;
      font-weight: bold;
    }

    .radar-num {
      font-size: 18px;
      font-weight: bold;
    }
  }
}
</style>



